<template>
    <keep-alive>
        <div :config="config" class="myranking" v-if="(isLogged || userId) && gameId">
            <template v-if="isRankable">
                <template v-if="rank">
                    <div v-if="inExpandList && displayChevron" class="chevron-action"><font-awesome-icon icon="chevron-right" size="3x" /></div>
                    <li v-if="inExpandList" :class="displayBigSize ? 'bigsize' : 'smallsize'"><span v-if="rank.name">{{ getTransChoice(rank.nbgames || 0, gamesPlayedTransKey, {'%nb_played_games%': rank.nbgames || 0, '%login%': rank.name}) }}</span></li>
                    <li class="score_rank" :class="(displayBigSize ? 'bigsize' : 'smallsize') + ' ' + (rank.nbgames < minGames ? 'hasprogress' : '')"><span>{{ getTrans('my_score') || getTrans('score') }} <b>{{ score || '-' }}</b></span>&nbsp;
                    <template v-if="rank.nbgames >= minGames"><span v-tooltip.auto="{content: this.getTrans(tooOldGameTrans, {'%login%': rank.name}), visible: !displayBigSize}"> - {{ userId == 'me' ? getTrans('my_rank') : getTrans('rank') }} <b>{{ rank.rank || defaultRankDesc }}</b> <span v-if="rank.rank <= 3 && rank.rank != null" v-tooltip.auto="getTrans('on_podium')" class="fanet fanet-stack anet-ranked"></span></span>&nbsp;
                    <span @click="$emit('show-my-rank', rank.rank)" v-if="activateShowMe && isLogged && userId == 'me' && rank.rank != null" class="btn btn-primary">{{ getTrans(showMeTrans, {'%login%': rank.name}) }}</span></template>
                    <template v-else>
                        <div class="progress" v-tooltip.auto="{content: getTrans('missing_games', {'%nbgames%': (minGames - (rank.nbgames || 0))})}"><div class="text-progress">{{ rank.nbgames || 0 }}/{{ minGames }}</div><div class="progress-bar progress-bar-striped active" role="progressbar" :aria-valuenow="progress" aria-valuemin="0" aria-valuemax="100" :style="{width: progress+'%'}"></div></div>
                    </template>
                    </li>
                    <li v-if="inExpandList" :class="'ranked ' + (displayBigSize ? 'bigsize' : 'smallsize') + ' ' + (rank.nbgames < minGames ? 'hasprogress' : '') + ' ' + (rank.rank && rank.rank <= 3 ? 'onpodium' : '')">
                        <span>{{ getTrans('karma') }} <span class="karma" v-html="karma"></span>
                        &nbsp;&nbsp;-&nbsp;&nbsp;</span><span class="history-game"><a :href="urlFor('history-game', {'id': gameId}) + userQueryPath" @click.prevent.stop="goHistory">{{ getTrans('game_history_link') }}</a></span>
                    </li>
                    <li v-if="inExpandList && displayBigSize" class="bigsize"><span>{{ getTrans('latest_game') }}<time :data-timestamp="rank.lastgame"><template v-if="['xs', 'xxs'].includes($mq)">{{ $moment.unix(rank.lastgame).format('L HH:mm') }}</template><template v-else>{{ $moment.unix(rank.lastgame).format('LLLL') }}</template></time></span></li>
                </template>
                <template v-else>
                    <template v-if="inExpandList">
                        <div v-if="inExpandList && displayChevron" class="chevron-action"><font-awesome-icon icon="chevron-right" size="3x" /></div>
                        <li :class="displayBigSize ? 'bigsize' : 'smallsize'"><span>{{ getTransChoice(0, gamesPlayedTransKey, {'%nb_played_games%': '-'}) }}</span></li>
                        <li :class="displayBigSize ? 'bigsize' : 'smallsize'"><span>{{ getTrans('score') }} -</span></li>
                        <li :class="'unranked ' + (displayBigSize ? 'bigsize' : 'smallsize')"><span>{{ getTrans('karma') }} -</span></li>
                    </template>
                </template>
            </template>
        </div>
    </keep-alive>
</template>

<script>
import HTTP from '@/httpapi';
import Games from '@api/games';
import User from '@api/user';
import GenericMixin from '@mixins/generic';

const MIN_GAME_TO_BE_RANKED = 20;
const GAMES_IN_VIABLE_DAYS = 15;

export default {
    name: 'my-ranking',

    mixins: [GenericMixin],

    props: ['game'],

    computed: {
        minGames () {
            return MIN_GAME_TO_BE_RANKED;
        },

        progress () {
            return Math.floor((this.rank.nbgames * 100) / MIN_GAME_TO_BE_RANKED);
        },

        gameId () {
            return this.game || this.config.game_id;
        },

        inExpandList () {
            return this.config.expandedlist || false;
        },

        displayChevron () {
            return (typeof this.config.chevron === 'undefined' || this.config.chevron);
        },

        displayBigSize () {
            return this.config.list_big || false;
        },

        activateShowMe () {
            return this.config.activate_show_me || false;
        },

        userId () {
            return this.config.userId || 'me';
        },

        karma () {
            if (this.rank.karma === null) {
                return '-';
            }

            const bullet = '&bull;';

            return bullet.repeat(Math.min(5, Math.trunc(this.rank.karma / 20 + 1)));
        },

        score () {
            if (this.rank.score === null) {
                return '-';
            }

            return Math.floor(this.rank.score);
        },

        gamesPlayedTransKey () {
            if (this.userId === 'me') {
                return 'games_played';
            }

            return 'games_played_public';
        },

        isTooOldGameToRank () {
            let d = new Date();
            d = d.setDate(d.getDate() - GAMES_IN_VIABLE_DAYS);

            return this.rank.lastgame < d;
        },

        defaultRankDesc () {
            return (this.isTooOldGameToRank && this.displayBigSize ? this.getTrans(this.tooOldGameTrans, {'%login%': this.rank.name}) : '-');
        },

        tooOldGameTrans () {
            return this.isLogged && this.userId === 'me' ? 'too_old_game_to_rank' : 'too_old_game_to_rank_public';
        },

        showMeTrans () {
            return this.isLogged && this.userId === 'me' ? 'show_my_position' : 'show_position_public';
        },

        userQueryPath () {
            return this.userId === 'me' ? '' : '?u=' + this.userId;
        }
    },

    methods: {
        myRank () {
            if (this.gameId) {
                let t = this;
                t.currentRankGame = t.gameId;
                Games.rankables().then(function (rankables) {
                    t.isRankable = !!(rankables.data.rankingGames.find(function (rg) {
                        return rg === t.gameId;
                    }));
                    if (t.isRankable) {
                        User.rank(t.userId, t.gameId).then(function (user) {
                            t.rank = user.data.user;
                        }).catch(function (error) {
                            if (error.response.status === 404) {
                                // user not played in this game yet
                                t.rank = {
                                    nbgames: 0,
                                    rank: null,
                                    karma: null,
                                    score: 0
                                };
                            }
                        });
                    } else {
                        t.rank = null;
                    }
                });
            }
        },

        goHistory (ev) {
            if (this.isIn3rdParty) {
                window.open(ev.target.href);
            } else {
                window.location = ev.target.href;
            }
        }
    },

    mounted () {
        HTTP.ifInitialized(this.myRank);
    },

    updated: function () {
        var that = this;
        // This method used to refresh reused my-ranking component with new game/user data
        this.$nextTick(function () {
            if (that.currentRankGame !== that.game) {
                that.myRank();
            }
        });
    },

    data () {
        return {
            rank: null,
            isRankable: null,
            currentRankGame: this.gameId
        };
    }
};

</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/progress.scss';
@import '~sass/tooltip.scss';

.myranking /deep/ {
    position: relative;
    top: 5px;

    .fanet {
        color: map-get($asnet-colors, 'light-purple');
    }

    li {
        list-style: none;
        text-decoration: none;

        &.smallsize {
            font-size: 0.9em;

            &.hasprogress {
                height: 17px;
                position: relative;
                top: -4px;
            }

            span {
                top: -5px;

                &.fanet.anet-ranked {
                    height: 21px;
                }
            }

            &:nth-of-type(3):not(.unranked) span {
                top: -10px;

                &.karma {
                    top: 0px;
                }
            }
        }

        &.bigsize {

            &:nth-of-type(2) {
                height: 30px;

                &:not(.hasprogress) span {
                    position: relative;
                    top: 4px;
                }

                span.fanet.anet-ranked {
                    height: 21px;
                    top: -10px;
                }
            }

            &:nth-of-type(3) {
                height: 30px;

                span.fanet.anet-ranked {
                    height: 21px;
                    top: -10px;
                }
            }

            .progress {
                position: relative;
                top: 4px;
            }
        }

        &.score_rank {
            span {
                b {
                    color: map-get($asnet-colors, light-purple);
                }
            }
        }

        &.ranked {
            span {
                &.karma {
                    font-size: 2em;
                    line-height: 0.5em;
                    vertical-align: middle;
                }
                &.history-game {
                    &:hover {
                        background-color: map-get($asnet-colors, light-purple);

                        a {
                            color: white;
                        }
                    }
                    a {
                        text-decoration: none;
                        color: map-get($asnet-colors, light-purple);
                    }
                }
            }
        }
    }

    span {
        position: relative;
        top: 0;

        &.fanet {
            top:-4px;
        }
    }

    .clickable:hover {
        background-color: lightgray;
        cursor: pointer;
    }

    a {
        &.unclickable {
            cursor: default;
            pointer-events: none;
        }
    }

    &.unclickable a {
        cursor: default;
        pointer-events: none;
    }

    .chevron-action {
        float: right;
        height:50px;
        position: relative;
        text-align:center;
        bottom: 20px;
        vertical-align: middle;

        .fa-chevron-right {
            position: relative;
            top: 15px;
            color: lightgray;
            font-weight: bold;
        }
    }

    :hover > .chevron-action .fa-chevron-right {
        color: white;
    }
}
.panel .panel-body .row .col-content ul .myranking li.ranked span.history-game a, .grid-activities .activity .myranking li.ranked span.history-game a {
    color: map-get($asnet-colors, light-purple);
}
.panel .panel-body .row .col-content ul .myranking li.ranked span.history-game:hover a, .grid-activities .activity .myranking li.ranked span.history-game:hover a {
    color: white;
}
</style>



